* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #1f1f38;
    /* --color-bg: #e2e4e4; */
    --color-bg-variant: #19568b;
    /* --color-bg-variant: #e2e4e4; */
    /* --color-bg-variant: #6C91C2; */
    /* --color-bg-variant: #011d3b; */
    /* --color-bg-variant: #1d1e2c; */
    /* --color-bg-variant: #2b3a67; */
    /* --color-bg-variant: #3a405a; */
    /* --color-bg-variant: #0a2239; */
    /* --color-bg-variant: #2b4570; */
    --color-primary: #4db5ff;
    /* --color-primary: rgb(35, 35, 35); */
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    /* --color-white: rgb(35, 35, 35); */
    --color-light: rgba(255, 255, 255, 0.6);
    /* --color-light: #373F47; */

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition: all 400ms ease;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar  {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: linear-gradient(rgb(15, 15, 15), rgb(40, 40, 40), rgb(15, 15, 15));
    background-image: '/assets/background1.jpg';
    /* background: #FAF9F6; */
    color: var(--color-white);
    line-height: 1.7;
}

/* ===================== GENERAL STYLES ===============*/
.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ============== MEDIA QUERIES (MEDIUM DEVICES) =============*/
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* ============== MEDIA QUERIES (SMALL DEVICES) =============*/
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}